import logo from './logo.svg';
import './App.css';
import Navbar from './Navbar';
import Intro from './Intro';
import FormSection from './FormSection';
import ServicesSection from './ServicesSection';
import CallbackSection from './CallbackSection';
import SocialMediaBar from './SocialMediaBar';
import React from 'react';
import Helmet from 'react-helmet';
import Footer from './footer';
class App extends React.Component {
  constructor(props){
    super(props);
    this.state = {
        curY : 0,
        isMobile : false,
        dropList : false
    }
    
  }
  toggleDropList= ()=>{
    //console.log(this.state.dropList);
    this.setState(state=>({
      curY : state.curY,
      isMobile:state.isMobile,
      dropList: !state.dropList 
    }))

  }
  componentDidMount() {
    if (typeof window !== "undefined") {
      this.setState({curY:window.pageYOffset,isMobile:window.innerWidth<=770});
      

      window.onscroll = () => {
        let currentScrollPos = window.pageYOffset;
        // let maxScroll = document.body.scrollHeight - window.innerHeight;
        // //console.log(currentScrollPos)
        // //if (currentScrollPos > 45 && currentScrollPos < maxScroll) {
        //   
        this.setState({curY : currentScrollPos,isMobile : window.innerWidth<=770});
        
      }
      window.onresize = () =>{
        
        //console.log(window.innerWidth)
        this.setState({curY : window.pageYOffset,isMobile:window.innerWidth<=770})
      }
    }
  }
  render(){
    return (
      
      <div className="App" style={{textAlign:"left"}}>
        <Helmet>
              <meta name="viewport" content="width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=no"/>
        </Helmet>
        
        {/* <SocialMediaBar isMobile = {this.state.isMobile}/> */}
        <Navbar isMobile = {this.state.isMobile} curY = {this.state.curY} handleChange = {this.toggleDropList}/>
        <Intro dropList = {this.state.dropList} isMobile = {this.state.isMobile}/>
        <FormSection/>
        <ServicesSection/>
        <CallbackSection/>
        <Footer/>
      </div>
    )
  }
}

export default App;
